/* Header */

  #header {
    @include color-typography(invert);
    @include vendor('align-items', 'center');
    @include vendor('display', 'flex');
    @include vendor('flex-direction', 'column');
    @include vendor('justify-content', 'flex-end');
    @include vendor('pointer-events', 'none');
    @include vendor('user-select', 'none');
    height: 20rem;
    padding-bottom: (_size(padding) * 4);
    position: relative;
    text-align: center;
    z-index: 2;

    .logo {
      // @include vendor('transition', (
      //   'border-color #{_duration(transition)} ease-in-out',
      //   'color #{_duration(transition)} ease-in-out',
      //   'opacity 0.5s ease',
      //   'transform 0.5s ease',
      //   'visibility 0.5s'
      // ));
      @include vendor('pointer-events', 'auto');
      border-style: solid;
      border-color: _palette(invert, border);
      border-width: 5px !important;
      font-family: _font(family-heading);
      font-size: 2.25rem;
      font-weight: _font(weight-heading);
      letter-spacing: 0.075em;
      line-height: 1;
      padding: 1rem 1.75rem;
      text-transform: uppercase;
      visibility: visible;

      &:hover {
        border-color: _palette(invert, accent) !important;
        color: _palette(invert, accent) !important;
      }
    }

    @include breakpoint(medium) {
      height: 14rem;
      padding-bottom: (_size(padding) * 2);
    }

    @include breakpoint(small) {
      padding-bottom: (_size(padding) * 1.5);

      .logo {
        font-size: 1.75rem;
        border-width: 3px !important;
      }
    }
  }
